.latest_tech_background{
    background-color: #c8e6e7;
}

.latest_tech_heading{
    width: 70%;
    /* border: 1px solid red; */
    font-size: 1.5rem;
    text-align: center;
    margin: 0 auto;
    padding: 20rem auto;
}
.latest_tech_heading h1{
    padding: 2.5rem;
}

.latest_tech_heading h4{
    color: grey;
    font-size: 1.2rem;
    padding-bottom: 3rem;
}

.latest_tech_icons{
    /* border: 1px solid red; */
    width: 85%;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.latest_tech_icon{
    /* border: 1px solid red; */
    margin: 1rem 2.7rem;
}

.latest_tech_icon_individual i{
    font-size: 2.5rem;
    margin: 0 2rem;
    background-color: blue;
    color: white;
    padding: 1.2rem 0.8rem;
    border-radius: 50%;
}

.latest_tech_icon p{
    margin: 1rem auto;
    text-align: center;
    font-weight: bolder;
}

.latest_tech_button{
    /* border: 1px solid red; */
    display: flex;
    justify-content: center;
    align-items: center;
}

.latest_tech_background a{
    background-color: #ff5c26;
    border-radius: 2rem;
    padding: .8rem 3rem;
    text-decoration: none;
    font-size: 1.1rem;
    font-weight: bolder;
    text-align: center;
    margin: 2rem 0;
    color: white;
}

.latest_tech_background a:hover{
    background-color: aquamarine;
    color: #ff5c26;
}

@media only screen and (max-width: 768px) {
    .latest_tech_heading {
        width: 85%;
    }
    .latest_tech_heading h1{
        padding: 1rem;
    }

    .latest_tech_icon{
        margin: 0;
    }

    
}