/* Hamburgerrrrrrrrrrrrrrrrrrrrrrrrrrrrrrrrrrrr */
.menu-wrap {
    display: block;
    position: absolute;
    top: 4%;
    /* right: 1%; */
    z-index: 100;
    /* border: 1px solid red; */
    width: 3%;
    height: 10%;
}

.menu-wrap .toggler {
    position: absolute;
    border: 1px solid white;
    top: 0;
    left: 0;
    z-index: 2;
    cursor: pointer;
    width: 50px;
    height: 50px;
    opacity: 0;
}

.menu-wrap .hamburger {
    position: absolute;
    top: 80%;
    left: 0;
    z-index: 1;
    width: 2.5rem;
    height: 50px;
    /* padding: 1rem; */
    /* border: 1px solid grey; */
    border-radius: 6px;
    /* background: var(--primary-color); */
    display: flex;
    align-items: center;
    justify-content: center;
}

/* Hamburger Line */
.menu-wrap .hamburger>div {
    position: relative;
    flex: none;
    width: 100%;
    height: 1.5px;
    background: black;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: -8rem;
    transition: all 0.4s ease;
}

/* Hamburger Lines - Top & Bottom */
.menu-wrap .hamburger>div::before,
.menu-wrap .hamburger>div::after {
    content: "";
    position: absolute;
    z-index: 1;
    top: -10px;
    width: 100%;
    height: .1rem;
    background: inherit;
    /* background: white; */
}

/* Moves Line Down */
.menu-wrap .hamburger>div::after {
    top: 10px;
}

/* Toggler Animation */
.menu-wrap .toggler:checked+.hamburger>div {
    transform: rotate(135deg);
}

/* Turns Lines Into X */
.menu-wrap .toggler:checked+.hamburger>div:before,
.menu-wrap .toggler:checked+.hamburger>div:after {
    top: 0;
    transform: rotate(90deg);
}

/* Rotate On Hover When Checked */
.menu-wrap .toggler:checked:hover+.hamburger>div {
    transform: rotate(225deg);
    /* border: 1px solid white; */
}

/* Show Menu */
.menu-wrap .toggler:checked~.menu {
    visibility: visible;
}

.menu-wrap .toggler:checked~.menu>div {
    /* transform: scale(1); */
    transition-duration: var(--menu-speed);
    background-color: wheat;
    left: 0%;
}

.menu-wrap .toggler:checked~.menu>div>div {
    opacity: 1;
    background-color: #ff5c26;
    transition: opacity 0.2s ease 0.4s;
    border-radius: 5px;
}

.menu-wrap .menu {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    visibility: hidden;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid green;
}

.menu-wrap .menu>div {
    /* background: var(--overlay-color); */
    background: var(--blue_navbar);
    /* border-radius: 50%; */
    width: 100%;
    height: 100vh;
    display: flex;
    flex: none;
    align-items: center;
    justify-content: center;
    left: 100%;
    /* transform: scale(0); */
    transition: all 0.4s ease;
    z-index: 1000 !important;
    position: fixed;
}

.menu-wrap .menu>div>div {
    text-align: center;
    max-width: 90vw;
    max-height: 100vh;
    opacity: 0;
    transition: opacity 0.4s ease;
}

.menu-wrap .menu>div>div>ul>li {
    list-style: none;
    color: #fff;
    font-size: 1.5rem;
    padding: 1rem 1.2rem;
}

.menu-wrap .menu>div>div>ul>li>a {
    /* color: rgb(255, 114, 89); */
    color: black;
    font-weight: bolder;
    font-size: 1.7rem;
    /* color: rgb(41, 40, 40); */
    padding: 5px 10px;
    /* border-radius: 100px; */
    text-decoration: none;
    transition: color 0.4s ease;
}







@media only screen and (max-width: 768px) {

    .first_div{
        flex-direction: column;
    }
    .column_1{
        width: 90%;
    }
    .the_mobile_wallet{
        margin: 1rem ;
        text-align: right;
    }
    .column_3{
        color: black;
    }

    .menu-wrap{
        /* border: 1px solid red; */
        left: 90%;
        top: -3%;
        width: 3rem;
    }
    .menu-wrap .toggler{
        border: 1px solid white;
        top: 45%;
    }

    .column_3{
        width: 90%;
        text-align: left;
    }


    .second_section h1{
        width: 90%;
        
    }
    .three_features{
        flex-direction: column;
    }
    .feature{
        border: none;
        width: 85%;
    }

    .section_3_1_image{
        display: none;
    }

    .section_3_2{
        flex-direction: column;
    }
    .section_3_2_image img{
        width: 170%;
    }
    .section_3_2_text{
        width: 85%;
    }

    
}