.download_section_main_div{
    /* border: 1px solid red; */
    display: flex;
    justify-content: space-around;
    width: 90%;
    margin: 0 auto;
    align-items: center;
}

.download_section_left{
    width: 50%;
}

.download_section_left h1{
    margin: 5rem 0 4rem 0;
    width: 90%;
    font-size: 2.6rem;
}

.download_section_left p{
    color: rgb(84, 84, 84);
    font-weight: bolder;
    margin: 3rem 0;
    line-height: 1.8rem;
}

.download_section_left h3{
    font-size: 1.8rem;
}

.qrcode_div{
    display: flex;
    /* border: 1px solid green; */
    /* justify-content: flex-start; */
}
.qrcode{
    /* border: 1px solid red; */
    width: 40%;
}

.qrcode img{
    /* border: 1px solid red; */
    width: 70%;
    margin: 1rem 0;
    border-radius: .6rem;
    background-color: white;
    padding: 4px;
}


.download_section_right{
    /* border: 1px solid red; */
    display: flex;
    justify-content: center;
    width: 45%;
}

.playstore{
    /* border: 1px solid red; */
    margin: 1rem 0;
    /* width: 90%; */
}

.playstore_1{
    /* border: 1px solid red; */
    background-color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 1rem;
    width: 70%;
    margin-bottom: 1rem;
    cursor: pointer;
}

.playstore_1 i{
    font-size: 3rem;
    color: white;
    padding: 1rem;
}

.playstore_1 h2{
    color: white;
    font-size: 1.3rem;
    padding-right: 1rem;
}


@media only screen and (max-width: 768px) {

    .download_section_main_div{
        width: 90%;
    }
    .download_section_right{
        display: none;
    }

    .download_section_left{
        width: 100%;
        text-align: center;
    }

    .qrcode_div{
        flex-direction: column;
        align-items: center;
    }
    .playstore_1{
        margin: 2rem auto;
    }
}