.footer_main_class{
    background-color: rgba(200,230,231,255);
}

.footer_class_inner_margin{
    padding: 1rem;
}

.upper_section{
    /* border: 1px solid red; */
    display: flex;
    justify-content: space-between;
}

.upper_section_left{
    width: 18%;
    /* border: 1px solid green; */
    display: flex;
}

.upper_section_left_left{
    width: 70%;
    display: flex;
    justify-content: center;
    align-items:flex-start;
}
.upper_section_left_left img{
    width: 60%;
}

.upper_section_left_right h2{
    color: white;
    display: inline-block;
    background-color: #ff5c25;
    padding: 3px;
    margin-bottom: 5px;
    /* margin-left: -1rem; */
}

.upper_section_right{
    /* border: 1px solid green; */
    display: flex;
    height: 4rem;
    /* width: 40%; */
}

.upper_section_right a{
    padding: 1.2rem;
    text-decoration: none;
    color: black;
    font-weight: bolder;
}

.upper_section_right a:hover{
   color: rgb(109, 109, 109);
}

.lower_section{
    /* border: 1px solid green; */
    margin-top: 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.lower_section_1 h3, .lower_section_1 h2 a{
    background-color: black;
    display: inline-block;
    color: white;
    text-decoration: none;
    margin: .15rem 1rem;
}

.social_icons a i{
    background-color: black;
    margin: 0 .6rem;
    border-radius: 50%;
    padding: .7rem;
    font-size: 1.2rem;
    color: white;
    cursor: pointer;
}

.social_icons a i:hover{
    color: white;
    background-color: rgb(39, 136, 255);
}


@media only screen and (max-width: 768px) {

    .upper_section{
        flex-direction: column;
    }

    .upper_section_left{
        width: 85%;
    }

    .upper_section_right{
        flex-wrap: wrap;
        justify-content: center;
        margin: 1.5rem 0;
    }

    .lower_section{
        flex-direction: column;
    }

    .lower_section_1{
        margin: 1rem auto;
        text-align: center;
    }
    .lower_section_2{
        margin: 1.4rem auto;
    }
    .lower_section_3{
        margin-bottom: 2rem;
    }

}