.phone {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 300px;
    height: 600px;
    background: #1b2e35;
    border-radius: 20px;
    padding: 8px;
    margin: 3rem;
}
.phone-mirror {
    position: relative;
    width: 98%;
    height: 595px;
    background-color: rgba(165,224,226,255);
    border-radius: 20px;
}
.topWrapper {
    position: relative;
    width: 130px;
    height: 20px;
    margin: 0px auto 0 auto;
    float: left;
    left: 30%;
    background-color: #1b2e35;
    border-radius: 0 0 40px 40px;
}
.line-rec {
    position: relative;
    width: 30px;
    margin: 0px auto 0 auto;
    float: left;
    left: 23%;
    top: 3.7px;
    border-radius: 5px;
    border: 3px solid rgb(214, 214, 214);
}
.camera {
    position: relative;
    float: left;
    margin-right: 8px;
    left: 25%;
    width: 8px;
    height: 8px;
    border-radius: 30px;
    border: 3px solid rgb(214, 214, 214);
}

.camera_2 {
    position: relative;
    float: left;
    margin-right: 8px;
    left: 27%;
    width: 5px;
    height: 5px;
    top: 2%;
    border-radius: 30px;
    border: 3px solid rgb(214, 214, 214);
}

.mobile_upper_part{
    width: 80%;
    margin: 0 auto;
    margin-top: -1.4rem;
    font-size: 1.5rem;
    display: flex;
    justify-content: space-between;
}
.text h1{
    text-align: center;
}

.text_2{
    /* border: 1px solid white; */
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: .5rem 0;
}

.text img{
    width: 15%;
    display: inline;
    
}
.text h2{
    display: inline;
    font-size: 1.5rem;
    color: #ff5c25;
    /* margin-bottom: 1rem; */
}

.apps{
    background-color: white;
    border-radius: 1rem;
    margin-top: 1rem;
}




.latest_tech_icons_apps{
    /* border: 1px solid red; */
    width: 100%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.latest_tech_icon_apps{
    /* border: 1px solid red; */
    width: 32.5%;
    margin: 0.6rem auto;
    text-align: center;
    cursor: pointer;
}

.latest_tech_icon_individual_apps i{
    font-size: 1.4rem;
    margin: 0 auto;
    background-color: blue;
    color: white;
    padding: .6rem 0.4rem;
    border-radius: 50%;
}

.latest_tech_icon_apps p{
    margin: 1rem auto;
    text-align: center;
    font-weight: bolder;
}

.latest_tech_button_apps a{
    /* border: 1px solid red; */
    display: inline-block;
    margin: .3rem 5rem;
    border-radius: 1rem;
    padding: .5rem 3rem;
    text-align: center;
    background-color: #a5e0e2;
    text-decoration: none;
    color: black;
    font-weight: bolder;
}